<template>
   <div class='updatecoach'>
       <div class='top'>
           <span class='fundInfo' @click='goback()'>会员卡管理</span>
           <img src='../../assets/images/icon_nav_arrow.png'/>
           <span class='branch'>详情</span>
       </div>
       <div class='card'>{{cardName}}</div>
       <div class='personInfo info'>
           <div class='personleft'>
              <div class='secInfo firstsec'>
                   <span class='sec_left'>卡号：</span>
                   <span class='sec_right'>{{form.code}}</span>
               </div>
               <div class='secInfo'>
                   <span class='sec_left'>绑定会员：</span>
                   <span class='sec_right'>{{form.personName}}</span>
               </div>
               <div class='secInfo'>
                   <span class='sec_left'>联名会员：</span>
                   <span class='sec_right'>{{form.combinationPersonName}}</span>
               </div>
               <div class='secInfo'>
                   <span class='sec_left'>有效时间：</span>
                   <span class='sec_right'>有效期至 {{form.rechargeExpireTime | timeformer}}</span>
                   <span class='edit' @click='updateTime'><img src='../../assets/images/btn_edit.svg'/>修改</span>
               </div>
               <div class='secInfo'>
                   <span class='sec_left'>使用门店：</span>
                   <span class='sec_right'>{{form.applyBranchNames}}</span>
               </div>
           </div>
           <div class='personright'>
             <div class='secInfo firstsec'>
                   <span class='sec_left'>实体卡号：</span>
                   <span class='sec_right'>{{form.physicalCardNo ? form.physicalCardNo : '无'}}</span>
                   <span class='edit' v-if='form.physicalCardNo' @click="updateCardNo('card')"><img src='../../assets/images/btn_edit.svg'/>修改</span>
               </div>
               <div class='secInfo firstsec'>
                   <span class='sec_left'>会员卡类型：</span>
                   <span class='sec_right'>{{form.cardType | cardType}}</span>
               </div>
               <div class='secInfo'>
                   <span class='sec_left'>会员卡状态：</span>
                   <span class='sec_right'>{{form.status | cardStatus}}</span>
               </div>
               <div class='secInfo'>
                   <span class='sec_left'>{{form.cardType === 'TIMES' ? '剩余次数：' : '余额：'}}</span>
                   <span class='sec_right'>{{form.balance}}{{form.cardType === 'TIMES' ? ' 次' : form.cardType === 'PREPAID' ? ' 元' : ' 天'}}</span>
                   <span class='edit' @click='modifyMoney' v-if="form.cardType === 'TIMES' || form.cardType === 'PREPAID'"><img src='../../assets/images/btn_edit.svg'/>修改</span>
               </div>
               <div class='secInfo' v-if="form.cardType !== 'DATELIMIT' && form.cardType !== 'PACKAGE'">
                   <span class='sec_left'>{{form.cardType === 'TIMES' ? '累计充值额度：' : '累计充值金额：'}}</span>
                   <span class='sec_right'>{{form.totalAmount}}{{form.cardType === 'TIMES' ? ' 次' : ' 元'}}</span>
               </div>
               <div class='secInfo' v-if="form.cardType === 'DATELIMIT' || form.cardType === 'PACKAGE'">
                   <span class='sec_left'>累计实收金额：</span>
                   <span class='sec_right'>{{form.totalActualIncome}} 元</span>
               </div>
           </div>
           <div class='personlast' v-if="form.cardType !== 'DATELIMIT' && form.cardType !== 'PACKAGE'">
               <div class='secInfo'>
                   <span class='sec_left'>累计实收金额：</span>
                   <span class='sec_right'>{{form.totalActualIncome}} 元</span>
               </div>
           </div>
       </div>
       <div class='recordText'>会员卡变更记录</div>
       <div class='editInfo'>
         <Table  :tableData="tableData" :tableKey="tableKey" :tabhide="tabhide" ref="Table" :total="total" :isPage = "isPage" :pageSize = "pageSize"
       v-on:changeVal="changeParam"></Table>
       </div>
        <div class='recharge'>
           <div class='form'>
             <el-dialog title="修改有效时间" :visible.sync="dialogFormVisible">
                <el-form :model="ruleform" :rules='rules' ref="ruleform" label-width="80px" >
                  <el-form-item label="有效期至" class='formNew expire' prop="expireDate" :class="isActive === 1 ? 'border-active' : ''">
                    <el-date-picker
                      v-model="ruleform.expireDate"
                      type="date"
                      placeholder="选择日期"
                      @focus="inDiv(1)"
                      @blur="outDiv(1)"
                      :picker-options="pickerOptions1">
                    </el-date-picker>
                  </el-form-item>
                  <el-form-item label="原因：" class='formNew TextArea' :class="isActive === 2 ? 'border-active' : ''">
                    <el-input v-model="ruleform.cause" class='inputNew'  type="textarea" placeholder="请输入" @focus="inDiv(2)" @blur="outDiv(2)"  :maxlength="255"></el-input>
                </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                  <el-button @click="resetForm('ruleform')">取 消</el-button>
                  <el-button type="primary" @click="submit('ruleform')">确 定</el-button>
                </div>
              </el-dialog>
           </div>
        </div>
        <cardMoney :dialogFormVisible='money' :cardType="cardType" :text="text" :cardObj="cardObj" @closeForm="closeForm"></cardMoney>
   </div>
</template>
<script>
import {getSessionStorage, isApiResponseOk} from '../../utils/com.js'
import {updatemember, getpercard, getcardchangeRecord, updateCardDate} from '../../services/service.js'
import Table from '../../components/Table'
import cardMoney from '@/components/modifyCardMoney'
export default {
  data () {
    return {
      form: {
        totalActualIncome: '',
        balance: '',
        status: '',
        cardType: '',
        gymBrandId: '',
        personId: '',
        code: '',
        rechargeExpireTime: '',
        peronName: '',
        personSex: ''
      },
      pickerOptions1: {
        disabledDate(time) {
          return time.getTime() < Date.now() - 8.64e7;
        }
      },
      ruleform: {
        expireDate: '',
        cause: '',
        userCardCode: '',
        rechargeCode: ''
      },
      options: [
        {label: '搏击私教课', value: '1', checked: false},
        {label: '精英私教课', value: '2', checked: false},
        {label: '主管私教课', value: '3', checked: false},
        {label: '健身冠军私教课', value: '4', checked: false},
        {label: '普通常规私教课', value: '5', checked: false}
      ],
      isShow: false,
      rules: {
        expireDate: [{required: true, message: '有效时间不能为空', trigger: 'change'}]
      },
      dialogFormVisible: false,
      personId: '',
      userCardCode: '',
      tableKey: [
        {name: '操作时间', value: 'createTime', nor: true},
        {name: '操作人', value: 'operator', nor: true},
        {name: '操作内容', value: 'content', nor: true},
        {name: '变动次数', value: 'varValue', nor: true},
        {name: '实收金额', value: 'actualPay', nor: true},
        {name: '支付方式', value: 'paymentMode', nor: true},
        {name: '有效期至', value: 'expireTime', nor: true},
        {name: '备注', value: 'note', nor: true}
      ],
      tableData: [
      ],
      isPage: true,
      total: 0,
      pageSize: 5,
      tabhide: true,
      para: {pageSize: 5, currentPage: 1},
      cardName: '',
      isActive: -1,
      money: false,
      cardType: '',
      text: '',
      cardObj: {}
    }
  },
  components: {
    Table,
    cardMoney
  },
  created () {
    this.personId = this.$route.query.personId
    this.userCardCode = this.$route.query.card
    this.para.userCardCode = this.$route.query.card
    this.cardName = this.$route.query.cardName
    if (this.personId) {
      this.getpercard()
      this.text = '修改会员卡'
    }
    this.getcardchangeRecord(this.para)
  },
  methods: {
    show () {
      this.isShow = !this.isShow
    },
    // 列表选择
    selcheck (item) {
      item.checked = !item.checked
    },
    // 删除选择
    deleteSel (item) {
      if (item.checked) {
        item.checked = false
      }
    },
    // 修改有效时间
    submit (ruleForm) {
      let self = this
      this.$refs[ruleForm].validate((valid) => {
        if (valid) {
          if (self.ruleform.expireDate) {
            self.ruleform.expireDate = new Date(self.ruleform.expireDate).getTime()
          }
          self.ruleform.rechargeCode = self.form.rechargeCode
          self.ruleform.userCardCode = self.userCardCode
          console.log('获取数据', self.ruleform.expireDate, self.form.rechargeCode, self.userCardCode)
          updateCardDate(self.ruleform).then(function (res) {
            if (isApiResponseOk(res)) {
              self.$message({
                showClose: true,
                message: '更新成功',
                type: 'success'
              })
              self.resetForm('ruleform')
              self.getpercard ()
              self.getcardchangeRecord(self.para)
            }
          })
        }
      })
    },
    // 返回到学员管理页面
    goback () {
      this.$router.push({name: 'memberCard'})
    },
    // 修改时间
    updateTime () {
      this.dialogFormVisible = true
      this.ruleform = {}
    },
    // 根据ID获取相应的数据
    async getpercard () {
      let data = {
        personId: this.personId,
        userCardCode: this.userCardCode
      }
      const res = await getpercard(data)
      if (isApiResponseOk(res)) {
        this.form = res.data
        switch (this.form.cardType) {
          case 'DATELIMIT':
            this.tableKey[3].name = '变动天数'
            return
          case 'TIMES':
            this.tableKey[3].name = '变动次数'
            return
          case 'PREPAID':
            this.tableKey[3].name = '变动金额'
            return
          case 'PACKAGE':
            this.tableKey[3].name = '变动天数'
            return
        }
        console.log('是否成功', res)
      }
    },
    // 获取会员卡操作记录
    async getcardchangeRecord (data) {
      const res = await getcardchangeRecord (data)
      if (isApiResponseOk(res)) {
        this.tableData = res.data.record
        this.total = res.data.totalNum
      } else {
        this.isPage = false
      }
    },
    // 更换页数
    changeParam (data) {
      this.para.currentPage = data
      this.getcardchangeRecord(this.para)
    },
    inDiv: function (data) {
      this.isActive = data
    },
    outDiv: function (data) {
      this.isActive = -1
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.dialogFormVisible = false
    },
    // 修改金额
    modifyMoney () {
      if (this.form.cardType === 'TIMES') {
        this.text = '修改剩余次数'
      } else {
        this.text = '修改余额'
      }
      Object.assign(this.cardObj, this.form)
      this.cardType = this.form.cardType
      this.money = true
    },
    closeForm () {
      this.money = false
      this.getpercard()
      this.getcardchangeRecord(this.para)
    },
    // 修改实体卡号
    updateCardNo (str) {
      this.text = '修改实体卡号'
      Object.assign(this.cardObj, this.form)
      let personId = this.personId
      Object.assign(this.cardObj, {personId})
      console.log('实体卡号数据', this.cardObj)
      this.cardType = 'card'
      this.money = true
    }
  }
}
</script>
<style lang="sass" scoped>
.updatecoach
    // .formNew
    //   .el-form-item__label
    //     &:before
    //       content: '' !important;
    //     &:after
    //       content: '*';
    //       color: #f56c6c;
    .info
        padding-left: .5rem;
        .personleft
            flex: 1;
            .secInfo
                .sec_right
                    font-size: .1rem;
                    color: #263238;
                    margin-left: .3rem;
                    font-weight: bold;
                .edit
                    background: #fff;
                    border: 1px solid;
                    border: 1px solid #E9EDEF;
                    border-radius: 2px;
                    font-size: .12rem;
                    color: #20C6BA;
                    padding: .05rem .12rem;
                    margin-left: .12rem;
                    cursor: pointer;
                    img
                      width: .11rem;
                      margin-right: .06rem;
        .personright
            flex: 1;
            .secInfo
                .sec_right
                    font-size: .1rem;
                    color: #263238;
                    margin-left: .3rem;
                .edit
                    background: #fff;
                    border: 1px solid;
                    border: 1px solid #E9EDEF;
                    border-radius: 2px;
                    font-size: .12rem;
                    color: #20C6BA;
                    padding: .05rem .12rem;
                    margin-left: .12rem;
                    cursor: pointer;
                    img
                      width: .11rem;
                      margin-right: .06rem;
        .personlast
            flex: 1;
            .secInfo
                .sec_right
                    font-size: .1rem;
                    color: #263238;
                    margin-left: .3rem;
                    font-weight: bold;
    .card
        height: .6rem;
        line-height: .6rem;
        background: #fff;
        border-bottom: 1px solid #F4F7F9;
        font-size: 20px;
        color: #263238;
        padding-left: .3rem;
    .recordText
      font-size: .18rem;
      margin-top: .48rem;
      color: #8197A3;
    .editInfo
      background: #f3f7f9;
      margin-top: .12rem;

</style>




