<template>
  <div class="table">
    <div class='tableHead' v-show='!tabhide'>
            <el-row :gutter="16">
              <el-col :span="4"  v-for="item in tableQuery" :key="item.id" :class="{endCol: (item.type === 'end_date') ? true : false}">
                 <!--输入框-->
                  <div>{{item.text}}</div>
                  <el-input :placeholder="item.title || '请输入搜索内容'" v-if="item.type === 'input'" v-model="item.model"
                  :style="item.style" :size="item.size || 'default'" :suffix-icon="item.icon" :disabled="item.disabled"
                    :maxlength="item.maxlength || 30" :element-id="item.id" :readonly="item.readonly"
                  @on-change="changeValue" :name="item.name" :id="item.id" class='content'></el-input>
                  <!--单选下拉框-->
                  <el-select  :placeholder="item.title || '请输入搜索内容'" v-if="item.type === 'select'" v-model="item.model" :style="item.style" :disabled="item.disabled" clearable
                          :size="item.size || 'default'" @on-change="changeValue" :name="item.name" :id="item.id" :filterable="item.filterable">
                    <el-option v-for="s in item.selectArr" :value="s.value" :key="s.value" :label="s.label" :disabled="s.disabled"></el-option>
                  </el-select >
                  <!--多选下拉框-->
                  <el-select :placeholder="item.title || '请输入搜索内容'" v-if="item.type === 'multiple'" v-model="item.model" multiple :style="item.style" :disabled="item.disabled"
                          :size="item.size || 'default'" @on-change="changeValue" :name="item.name" :id="item.id" :filterable="item.filterable">
                    <el-option v-for="s in item.selectArr" :value="s.value" :key="s.value" :disabled="s.disabled">{{s.label}}</el-option>
                  </el-select>
                  <!--日期控件--><!--默认入参格式必须是yyyy-MM-dd 调接口再去掉横杠-->
                  <el-date-picker v-if="item.type === 'start_date'" v-model="item.model"
                               type="date" :placeholder="item.title" :class="item.class || 'defaultDate'"
                              :style="item.style || 'width:100%'" @on-change="startDateChange" @on-ok="changeValue" :name="item.name" :id="item.id" :title="item.altTitle"></el-date-picker>
                  <!--日期控件-->
                  <el-date-picker v-if="item.type === 'end_date'" v-model="item.model"
                         type="date" :placeholder="item.title" :class="item.class || 'defaultDate'"
                        :style="item.style || 'width:100%'" @on-change="endDateChange" @on-ok="changeValue" :name="item.name" :id="item.id" :title="item.altTitle" class='endDate'></el-date-picker>
              </el-col>
               <el-col :span="4" v-if="showNull" v-for="item in queryNull" :key="item.id">
                  &nbsp;
                </el-col>
              <el-col :span="4" class='btnquery'>
                <div class="head-btn-group" :class="{'align-right': showNull}">
                  <button @click="queryBtn" @keyup.enter.native="queryBtn">筛选</button>
                  <!-- <button @click="reset">重置</button> -->
                </div>
              </el-col>
            </el-row>
    </div>
    <!-- <div class='tabText' v-show="tabTxet">
      <div class='title'>会员卡管理<span>{{total}}</span></div>
        <div class='addMember' @click='addMember'>
            <img src='../assets/images/btn_add.png'>
            <span>添加会员卡</span>
      </div>
    </div> -->
    <div v-if="tableTitle" class="tableTitle">课程预约</div>
     <div class='tableBody'>
         <el-table :data="tableData" border  ref="multipleTable" tooltip-effect="dark"  align="center" @selection-change="handleSelectionChange" :cell-class-name="cellClass">
            <el-table-column
              v-if="isShowSelectionBtn"
              type="selection"
              width="70"
              align="center"
            >
            </el-table-column>
            <el-table-column v-for ='(item,key) in tableKey '
                v-if="item.nor"
                :key="key"
                :prop="item.value"
                :label="item.name"
                :type="item.selection"
                align="center"
                :formatter = formatter
                :class-name="item.class"
                >
                </el-table-column>
            <el-table-column v-else-if="item.operate" :label='item.name' align="center">
                <template slot-scope="scope">
                    <slot :name="item.value" :$index="scope.$index" :row="scope.row" >
                      <img :src="item.img" @click="handleClick(scope.$index)"/></slot>
                </template>
            </el-table-column>
            <el-table-column v-else-if="item.btn" :label='item.name' align="center" class-name="btngro">
                <template slot-scope="scope" className='btngroup'>
                  <div v-for='(item,key) in scope.row.btnGroup'
                  size="mini"
                  :key='key'
                  :value = 'item.text'
                  align = "center"
                  :disabled = 'item.disabled'
                  :style="item.style"
                  @click="handleBtn(item.text, scope.row)"
                  class='btn'
                  :class="item.text=='取消预约'? 'redText': ''"
                  >{{item.text}}</div>
                </template>
            </el-table-column>
            <el-table-column v-else-if="item.studentKind" :label='item.name' align="center">
                <template slot-scope="scope">
                    <div v-if='scope.row.vipPrice || scope.row.vipPrice === 0' class="flexCell" style="width: 100px;">
                      <img src='../assets/images/icon_vip.png' style="margin-right: 0.1rem"/>
                      <span>{{scope.row.vipPrice}}元/节</span>
                    </div>
                    <div v-if='scope.row.vip' style="width: 100%">
                      <img src='../assets/images/icon_vip.png' style="margin-top: -2px;margin-right: 0.05rem" v-if='scope.row.vip == true'/>
                      <span v-if='scope.row.vip == true'>会员</span>
                      <span v-if='scope.row.vip == false'>非会员</span>
                    </div>
                    <div v-if='scope.row.status' style="width: 100%">
                      <img src='../assets/images/icon_vip.png' style="margin-top: -2px;margin-right: 0.05rem" v-if='scope.row.status == 3'/>
                      <span v-if='scope.row.status == 3'>会员</span>
                      <span v-if='scope.row.status == 2'>非会员</span>
                    </div>
                </template>
            </el-table-column>
            <el-table-column v-else-if="item.applyGyms" :label='item.name' align="center">
                <template slot-scope="scope">
                  <el-popover trigger="hover" placement="right">
                    <div class='coachName'>
                      <li v-for='(item,index) in scope.row.applyGyms' :key='index'>{{item.name}}</li>
                    </div>
                    <div slot="reference" class="name-wrapper">
                      <span v-if="scope.row.applyGyms.length > 0">{{(scope.row.applyGyms instanceof Array) && scope.row.applyGyms[0].name && scope.row.applyGyms[0].name.length > 9 ? scope.row.applyGyms[0].name.substring(0,8) + '...' : scope.row.applyGyms[0].name}}</span>
                    </div>
                  </el-popover>
                </template>
            </el-table-column>
            <el-table-column v-else-if="item.brief" :label='item.name' align="center">
                <template slot-scope="scope">
                  <el-popover trigger="hover" placement="right">
                    <div class='coachName'>
                      {{scope.row.brief}}
                      <!-- <li v-for='(item,index) in scope.row.applyGyms' :key='index'>{{item.name}}</li> -->
                    </div>
                    <div slot="reference" class="name-wrapper">
                      <span v-if="scope.row.brief && scope.row.brief.length < 8">{{scope.row.brief}}</span>
                      <span v-if="scope.row.brief && scope.row.brief.length >= 8">{{scope.row.brief.substring(0,8)}}...</span>
                      <!-- {{(scope.row.applyGyms instanceof Array) && scope.row.applyGyms.length > 1 ? scope.row.applyGyms[0].name + '...' : scope.row.applyGyms[0].name}} -->
                    </div>
                  </el-popover>
                </template>
            </el-table-column>
            <el-table-column v-else-if="item.potentialNum" :label='item.name' align="center">
                <template slot-scope="scope">
                  <el-popover trigger="hover" placement="right">
                    <div class='coachName'>
                      <li v-for='(item,index) in scope.row.potentialGuestPOList' :key='index'>{{item.name}}</li>
                    </div>
                    <div slot="reference" class="name-wrapper">
                      <span v-if="scope.row.potentialGuestPOList.length > 0">{{scope.row.potentialGuestPOList.length}}</span>
                    </div>
                  </el-popover>
                </template>
            </el-table-column>
            <el-table-column v-else-if="item.picture" :label='item.name' align="center">
                <template slot-scope="scope">
                    <img style='width: 30px; height:30px;border-radius:15px;' :src="(scope.row.extraImage && scope.row.extraImage.indexOf('http') > -1) ? scope.row.extraImage : scope.row.personImage"
                    v-if='(scope.row.personImage || scope.row.extraImage)'/>
                    <img src='../assets/images/bg_pic_defult.png' v-if='!scope.row.extraImage && !scope.row.personImage'/>
                </template>
            </el-table-column>
            <el-table-column v-else-if="item.persoName" :label='item.name' align="center">
                <template slot-scope="scope">
                    <span>{{scope.row.extraName ? scope.row.extraName : scope.row.personName ? scope.row.personName : scope.row.phoneNum ? scope.row.phoneNum : scope.row.email}}</span>
                </template>
            </el-table-column>
            <el-table-column v-else-if="item.contact" :label='item.name' align="center">
                <template slot-scope="scope">
                    <span>{{scope.row.phoneNum ? (scope.row.phoneNum.substring(0, 3) + '-' + scope.row.phoneNum.substring(3, 7) + '-' + scope.row.phoneNum.substring(7, 11)): scope.row.email}}</span>
                </template>
            </el-table-column>
            <el-table-column v-else-if="item.courseStatus" :label='item.name' align="center">
                <template slot-scope="scope">
                    <span  :class="{circle: true, active: (scope.row.reserveStatus.toUpperCase() == 'SUCCEED') ? true: false, success: (scope.row.reserveStatus.toUpperCase() == 'FINISHED') ? true: false, cancel: (scope.row.reserveStatus.toUpperCase() == 'EXPIRATION') ? true: false}" v-if="scope.row.reserveStatus"></span>
                    <span v-if="(scope.row.reserveStatus.toUpperCase()) == 'UNPAID'">预约未支付</span>
                    <span v-if="(scope.row.reserveStatus.toUpperCase()) == 'SUCCEED'">预约成功</span>
                    <span v-if="(scope.row.reserveStatus.toUpperCase()) == 'CANCEL'">取消成功</span>
                    <span v-if="(scope.row.reserveStatus.toUpperCase()) == 'FINISHED'">课程完成</span>
                    <!-- <span v-if="(scope.row.reserveStatus.toUpperCase()) == 'FINISHED'" class="cancle" @click="cnacleInvite(scope.row)">取消预约</span> -->
                    <span v-if="(scope.row.reserveStatus.toUpperCase()) == 'EXPIRATION'">课程过期</span>
                </template>
            </el-table-column>
            <el-table-column v-else-if="item.appointTime" :label='item.name' align="center">
                <template slot-scope="scope" v-if="scope.row.startTime">
                    <span>{{scope.row.startTime | timeformer}}&nbsp;{{scope.row.startTime | getMinuter}} - {{scope.row.endTime | getMinuter}}</span>
                </template>
                <template slot-scope="scope" v-if="scope.row.enterTime">
                    <span>{{scope.row.enterTime | timeformer}}&nbsp; &nbsp;{{scope.row.enterTime | getMinuter}}</span>
                </template>
            </el-table-column>
            <el-table-column v-else-if="item.seqNo" :label='item.name' align="center">
                <template slot-scope="scope">
                    <span>{{scope.row.seqNo+((currentPage-1)*pageSize)}}</span>
                </template>
            </el-table-column>
            <el-table-column v-else-if="item.toDetail" :label='item.name' align="center">
                <template slot-scope="scope">
                    <span class="toCardDetail" @click="toCardDetail(scope.row)">{{scope.row.name}}</span>
                </template>
            </el-table-column>
            <el-table-column v-else-if="item.hov1" :label='item.name' align="center">
                <template slot-scope="scope" >
                        <div class="tolip" v-if="scope.row.studentList && scope.row.studentList.length > 0">
                           <el-popover trigger="hover" placement="right">
                            <div class='coachName'>
                              <li v-for='(item,index) in scope.row.studentList' :key='index'>
                                <span v-if='item.personName'>{{item.personName}}</span>
                                <span v-if='!item.personName'>{{item.accountMobile}}</span>
                              </li>
                            </div>
                            <div slot="reference" class="name-wrapper">
                              {{scope.row.studentList.length}}
                            </div>
                          </el-popover>
                        </div>
                </template>
            </el-table-column>
            <el-table-column v-else-if="item.balance" :label='item.name' align="center">
                <template slot-scope="scope" >
                        <div class="tolip">
                           <el-popover trigger="hover" placement="right">
                            <div class='coachName' v-if="scope.row.balanceList && scope.row.balanceList.length > 0">
                              <li v-for='(item,index) in scope.row.balanceList' :key='index'>
                                <span v-if='item.balance'>{{item.startTime | timeformer}}{{item.startTime ? ' 到 ' : ''}}{{item.endTime | timeformer}}&nbsp;&nbsp;{{item.balance}}{{scope.row.vipCardType === 'DATELIMIT' || scope.row.vipCardType === 'PACKAGE' ? '天' : scope.row.vipCardType === 'TIMES' ? '次' : '元'}}</span>
                              </li>
                            </div>
                            <div slot="reference" class="name-wrapper" v-if="scope.row.vipCardType">
                              {{scope.row.balance ? scope.row.balance : 0}}{{scope.row.vipCardType === 'DATELIMIT' || scope.row.vipCardType === 'PACKAGE' ? ' 天' : scope.row.vipCardType === 'TIMES' ? ' 次' : ' 元'}}
                            </div>
                            <div slot="reference" class="name-wrapper" v-if="scope.row.type">
                              {{scope.row.balance ? scope.row.balance : 0}}{{scope.row.type === 'DATELIMIT' || scope.row.type === 'PACKAGE' ? ' 天' : scope.row.type === 'TIMES' ? ' 次' : ' 元'}}
                            </div>
                          </el-popover>
                        </div>
                </template>
            </el-table-column>
            <!-- <el-table-column v-else-if="item.hov1" :label='item.name' align="center">
                <template slot-scope="scope" >
                        <div class="tolip" v-if="scope.row.studentList.length > 0">
                           <el-popover trigger="hover" placement="right">
                            <div class='coachName'>
                              <li v-for='(item,index) in scope.row.studentList' :key='index'>
                                {{item}}
                                <span v-if='item.personName'>{{item.personName}}{{item.accountMobile}}</span>
                                <span v-if='!item.personName'>{{item.accountMobile}}</span>
                              </li>
                            </div>
                            <div slot="reference" class="name-wrapper">
                              {{scope.row.studentList.length}}
                            </div>
                          </el-popover>
                        </div>
                </template>
            </el-table-column> -->
            <el-table-column v-else-if="item.coach" :label='item.name' align="center">
                <template slot-scope="scope" >
                        <div class="tolip" v-if="(scope.row.coachNames instanceof Array)&& scope.row.coachNames.length > 0">
                           <el-popover trigger="hover" placement="right">
                            <div class='coachName'>
                              <li v-for='(item,index) in scope.row.coachNames' :key='index'>{{item}}</li>
                            </div>
                            <div slot="reference" class="name-wrapper">
                              {{(scope.row.coachNames instanceof Array) && scope.row.coachNames.length > 1 ? scope.row.coachNames[0] + '...' : scope.row.coachNames[0]}}
                            </div>
                          </el-popover>
                        </div>
                </template> 
            </el-table-column>
            <el-table-column v-else-if="item.freeList" :label='item.name' align="center">
                <template slot-scope="scope" >
                        <div class="tolip" v-if="(scope.row.coursePOList instanceof Array)&& scope.row.coursePOList.length > 0">
                           <el-popover trigger="hover" placement="right">
                            <div class='coachName'>
                              <li v-for='(item,index) in scope.row.coursePOList' :key='index'>{{item.courseName}}</li>
                            </div>
                            <div slot="reference" class="name-wrapper">
                              {{(scope.row.coursePOList instanceof Array) && scope.row.coursePOList.length > 1 ? scope.row.coursePOList[0].courseName + '...' : scope.row.coursePOList[0].courseName}}
                            </div>
                          </el-popover>
                        </div>
                </template>
            </el-table-column>
            <el-table-column v-else-if="item.cardUser" :label='item.name' align="center">
                <template slot-scope="scope" >
                        <div class="tolip">
                          {{scope.row.useOffNumber}} / {{scope.row.couponCount }}
                        </div>
                </template>
            </el-table-column>
            <el-table-column label="操作" v-if="operateBtn" :width="operWidth ? operWidth : 150" class-name="btngro" align="center">
              <template slot-scope="scope" className='btngroup'>
                  <div v-for='(item,key) in operateBtn'
                  size="mini"
                  :key='key'
                  :value = 'item.text'
                  align = "center"
                  @click.stop="handleBtn(item.text, scope.row)"
                  class='btn'
                  :style="item.style"
                  >{{item.text}}</div>
              </template>
            </el-table-column>
          </el-table>
     </div>
     <div class='pageination' v-if="isPage && total">
          <el-button v-if="isBtn" type="primary" round @click="submit" :class="[selectList.length > 0 ? '' : 'unclick']" suffix-icon="el-icon-check">全部提交</el-button>
          <div class="recordCount">
            本页 
            <span v-if="Math.ceil(total/pageSize) != currentPage">{{total>pageSize ? pageSize : total}}</span>
            <span v-if="Math.ceil(total/pageSize) == currentPage">{{total>pageSize ? total%pageSize : total}}</span> 
            条， 共 <span>{{total}}</span> 条数据
          </div>
          <el-pagination
            background
            layout="slot,prev"
            @current-change="handleCurrentChange"
            :total=total
            :page-size="pageSize"
            :current-page="currentPage">
          <el-button class='firstPage' @click='goFitst' :disabled="predisabled">首页</el-button>
        </el-pagination>
        <el-pagination
            background
            layout="pager,next,slot"
            @current-change="handleCurrentChange"
            :total=total
            :page-size="pageSize"
            :current-page="currentPage">
          <el-button class='lastPage' @click='goLast' :disabled="nextdisabled">末页</el-button>
        </el-pagination>
     </div>
  </div>
</template>

<script>
import {timeformer, formatDate} from '../utils/com.js'
export default {
  name: 'Table',
  data () {
    return {
      options: [{
        value: 'WarmUp',
        label: '热身'
      }, {
        value: 'Resistance',
        label: '力量'
      }, {
        value: 'Aerobic',
        label: '有氧'
      }, {
        value: 'Stretch',
        label: '拉伸'
      }],
      inout: '',
      queryNull: 0,
      showNull: true,
      pageNumber: 0,
      size: 0,
      predisabled: false,
      nextdisabled: false,
      currentPage: 1
    }
  },
  mounted () {
    this.$nextTick(_ => {
      // if (this.total === 0) {
      //   this.isPage = false
      // } else {
      //   this.isPage = true
      // }
    })
  },
  watch: {
    'total': function () {
      this.size = this.total / this.pageSize
      this.pageNumber = (this.total % this.pageSize === 0) ? this.size : parseInt(this.size) + 1
      if (this.pageNumber === 1) {
        this.predisabled = true
        this.nextdisabled = true
      }
    }
  },
  props: ['tableData', 'tableKey', 'total', 'isShowSelectionBtn', 'isPage', 'operWidth', 'pageSize', 'tableQuery', 'tabhide', 'operateBtn', 'selectList', 'isBtn', 'tabTxet', 'tableTitle'],
  methods: {
    //  跳转到对应卡详情
    toCardDetail (row) {
      this.$router.push({name: 'memberDetail', query:{personId: row.personId, card: row.code, cardName: row.name}})
    },
    /**
     * description: 此方法控制每一行操作的按钮
     */
    handleBtn (index, row) {
      let data = {
        index: index,
        row: row
      }
      this.$emit('getRowdata', data)
    },
    handleCurrentChange (page) {
      this.currentPage = page
      this.page = page
      if (page === 1) {
        this.predisabled = true
        this.nextdisabled = false
      } else if (page === this.pageNumber) {
        this.nextdisabled = true
        this.predisabled = false
      } else {
        this.predisabled = false
        this.nextdisabled = false
      }
      this.$emit('changeVal', page)
    },
    handleCurrentChange1 (page) {
      console.log('获取数据', page)
    },
    // 获取首页
    goFitst () {
      let page = 1
      this.currentPage = page
      this.handleCurrentChange(page)
    },
    // 获取尾页
    goLast () {
      let pageSize = 10
      if (this.pageSize) {
        pageSize = this.pageSize
      }
      let num = 0
      if (parseInt(this.total) % parseInt(pageSize) > 0) {
        num = 1
      }
      let page = parseInt(this.total / pageSize) + num
      console.log('总条数', this.total, pageSize)
      this.currentPage = page
      this.handleCurrentChange(page)
    },
    /**
     * 多选框选择数据，数据传递到父组件当中
     */
    handleSelectionChange (val) {
      this.multipleSelection = val
      this.$emit('selectList', this.multipleSelection)
    },
    // 添加会员卡
    // addMember () {
    //   this.$emit('addMember')
    // },
    // 页面首页
    formatter (row, column, cellValue) {
      if ((column.property === 'time' || column.property === 'durationTime') && cellValue) {
        return <span>{cellValue}</span>
      }
      if (column.property == 'isActive') {
        if (cellValue == 2) {
          return '否'
        } else if (cellValue == 1) {
          return '是'
        }
      }
      return cellValue
    },
    // 获取查询条件，向父组件传值
    changeValue (event) {
      let queryObj = {}
      for (let i = 0; i < this.tableQuery.length; i++) {
        queryObj[this.tableQuery[i].name] = this.tableQuery[i].model
      }
      this.$emit('changeVal', queryObj)
    },
    // 开始时间change事件，在这里改变model值
    startDateChange (event) {
      for (let i = 0; i < this.tableQuery.length; i++) {
        if (this.tableQuery[i].name === 'START_DATE') {
          this.tableQuery[i].model = event
        }
      }
      this.changeValue()
    },
    // 结束时间change事件，在这里改变model值
    endDateChange (event) {
      for (let i = 0; i < this.tableQuery.length; i++) {
        if (this.tableQuery[i].name === 'END_DATE') {
          this.tableQuery[i].model = event.replace('00:00:00', '23:59:59')
        }
      }
      this.changeValue()
    },
    // 手动调用搜索方法，返回查询条件取值
    search () {
      let queryObj = {}
      for (let i = 0; i < this.tableQuery.length; i++) {
        if (!this.tableQuery[i].name) {
          alert('缺少属性名称,请检查')
        }
        queryObj[this.tableQuery[i].name] = this.tableQuery[i].model
      }
      return queryObj
    },
    // 点击查询按钮，返回查询条件取值
    queryBtn () {
      let queryObj = {}
      for (let i = 0; i < this.tableQuery.length; i++) {
        if (!this.tableQuery[i].name) {
          alert('缺少属性名称,请检查')
        }
        if (this.tableQuery[i].name === 'Professional') {
          queryObj['Professional'] = this.tableQuery[i].model
        } else if (this.tableQuery[i].name === 'Week') {
          queryObj['Week'] = this.tableQuery[i].model
        } else if (this.tableQuery[i].name === 'coachId') {
          queryObj['coachId'] = this.tableQuery[i].model
        } else if (this.tableQuery[i].name === 'startTime') {
          queryObj['startTime'] = (this.tableQuery[i].model === '' || this.tableQuery[i].model == null) ? '' : new Date(this.tableQuery[i].model).getTime()
        } else if (this.tableQuery[i].name === 'endTime') {
          queryObj['endTime'] = (this.tableQuery[i].model === '' || this.tableQuery[i].model == null) ? '' : new Date((formatDate(new Date(this.tableQuery[i].model), 'yyyy-MM-dd') + ' 23:59:59').toString()).getTime()
        } else {
          queryObj[this.tableQuery[i].name] = this.tableQuery[i].model.trim()
        }
      }
      this.$emit('getQueryParam', queryObj)
    },
    reset () {
      for (let i = 0; i < this.tableQuery.length; i++) {
        if (this.tableQuery[i].model && Array.isArray(this.tableQuery[i].model)) {
          this.tableQuery[i].model = []
        } else {
          this.tableQuery[i].model = ''
        }
      }
    },
    // 搜索条件中某些下拉框需要异步加载，但是无法随父页面重新绑定，在父组件调此方法传入参数，在子组件内重新赋值
    reloadValue (name, value) {
      for (let i = 0; i < this.tableQuery.length; i++) {
        if (this.tableQuery[i].name === name) {
          this.tableQuery[i].selectArr = value
        }
      }
    },
    cellClass({row, column, rowIndex, columnIndex}) {
      //第八列添加 red 类
      if(row.intensityLevel == '保持稳定'){
        return 'complete'
      }
    },
    cnacleInvite (item) {
      console.log(item)
      this.$confirm('确认取消？')
        .then(_=> {
          console.log('quxiaochengg')
        })
        .catch(_=>{});
    }
  }
  
}
</script>


