<template>
    <div class='recharge moneyUpdate'>
          <el-dialog :title="text" :visible.sync="dialogFormVisible" :before-close="handleClose">
            <!-- <div class='cardText'>{{cardList.cardName}}</div> -->
            <div class='personInfo' v-if="cardType !== 'card'">
                <div class='personLeft'>
                    <div class='fir'>{{cardType !== 'TIMES' ? '累计充值金额' :'累计充值额度'}}</div>
                    <div class='sec'>{{cardObj.totalAmount}}{{cardType !== 'TIMES' ? '元' :'次'}}</div>
                </div>
                <div class='personRight'>
                    <div class='fir'>{{cardType !== 'TIMES' ? '金额' :'剩余次数'}}</div>
                    <div class='sec'>
                        {{cardObj.balance}}{{cardType !== 'TIMES' ? '元' :'次'}}
                    </div>
                </div>
            </div>
            <el-form :model='form'  ref='form' :rules="rules">
                <div class='form' v-if="cardType !== 'card'">
                    <el-form-item label="修改余额：" class='formNew option money' prop='operatorMethod' :class="isActive === 1 ? 'border-active' : ''">
                        <el-radio v-model="form.operatorMethod" label="1">增加</el-radio>
                        <el-radio v-model="form.operatorMethod" label="2">减少</el-radio>
                    </el-form-item>
                </div>
                <el-form-item label="实体卡号：" class='formNew card' :class="isActive === 2 ? 'border-active' : ''">
                    <el-input v-model='form.physicalCardNo' type='text'></el-input>
                </el-form-item>
                <div class='form secform' v-if="cardType !== 'card'">
                    <div class='left'>
                        <el-form-item label=" " class='formNew  times' :class="isActive === 4 ? 'border-active' : ''">
                            <el-input v-model='form.addNum' type='text' :disabled="disabledAdd"></el-input><span>{{cardType !== 'TIMES' ? '元' :'次'}}</span>
                        </el-form-item>
                    </div>
                    <div class='right'>
                        <el-form-item label=" " class='formNew  times' :class="isActive === 5 ? 'border-active' : ''">
                            <el-input v-model='form.reduceNum' type='text' :disabled="disabledReduce"></el-input><span>{{cardType !== 'TIMES' ? '元' :'次'}}</span>
                        </el-form-item>
                    </div>
                </div>
                <el-form-item label="原因：" class='formNew option TextArea' :class="isActive === 6 ? 'border-active' : ''" prop='note' v-if="cardType !== 'card'">
                    <el-input v-model="form.note" class='inputNew'  type="textarea" placeholder="请输入" @focus="inDiv(6)" @blur="outDiv(6)" :maxlength="255"></el-input>
                </el-form-item>
            </el-form>
            <div slot='footer' class='dialog-footer'>
                <el-button @click="resetForm('form')">取消</el-button>
                <el-button type='primary' @click="submit('form')">确定</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
import {updateamount, updatePhyCardNo} from '../services/service.js'
import {isApiResponseOk, getSessionStorage} from '../utils/com.js'
export default {
    data () {
        return {
            form: {
                name: '',
                note: '',
                operatorMethod: '',
                addNum: '',
                reduceNum: '',
                physicalCardNo: ''
            },
            formLabelWidth: 100,
            cardList: {},
            isActive: -1,
            personpic: '',
            rules: {
                operatorMethod: [{required: true, message: '修改金额不能为空', trigger: 'change'}],
                note: [{required: true, message: '原因不能为空', trigger: 'blur'}]
            },
            num: 0,
            disabledAdd: false,
            disabledReduce: false
        }
    },
    props: ['dialogFormVisible', 'text', 'cardType', 'cardObj'],
    methods: {
        inDiv: function (data) {
            this.isActive = data
        },
        outDiv: function (data) {
            this.isActive = -1
        },
        handleClose () {
            let self = this
            this.$confirm('确认关闭？', {type: 'warning'})
                .then(_ => {
                self.resetForm('form')
                self.$emit('closeForm', close)
            })
            .catch(_ => {})
        },
        resetForm(formName) {
            this.$refs[formName].resetFields()
            this.$emit('closeForm', close)
        },
        submit(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    if (this.cardType !== 'card' ) {
                        if (!this.form.addNum && !this.form.reduceNum) {
                            this.$message({
                                showClose: true,
                                message: '金额不能为空',
                                type: 'warning'
                            })
                            return
                        } else {
                            // 修改金额
                            // 表示增加
                            let alterAmount
                            if ( this.form.operatorMethod === '1' ) {
                                alterAmount = this.form.addNum
                            } else if (this.form.operatorMethod === '2') {
                                alterAmount = this.form.reduceNum
                            }
                            console.log('数据', this.cardObj.code, this.form.operatorMethod, this.form.addNum, this.form.reduceNum, alterAmount)
                            this.updateAmount(this.cardObj.code, alterAmount, this.form.operatorMethod, this.form.note)
                        }
                    } else {
                        // 修改实体卡号
                        this.updateCard(this.cardObj.personId, this.cardObj.code, this.form.physicalCardNo)
                    }
                } else {

                }
            })
        },
        // 停卡操作
        async updateAmount (code, amount, operator, note) {
            const res = await updateamount(code, amount, operator, note)
            if (isApiResponseOk(res)) {
                this.$message({
                    showClose: true,
                    message: '修改成功',
                    type: 'success'
                })
                this.resetForm('form')
            }
        },
        // 修改实体卡号
        async updateCard (personId, code, physicalCardNo) {
            const res = await updatePhyCardNo(personId, code, physicalCardNo)
            if (isApiResponseOk(res)) {
                this.$message({
                    showClose: true,
                    message: '修改成功',
                    type: 'success'
                })
                this.resetForm('form')
            }
        }
    },
    watch: {
        'dialogFormVisible': function (newVal) {
            if (newVal) {
                // this.form = {}
            }
        },
        'form.addNum': function (newVal, oldVal) {
            let FormatReg = /^\d+$/
            if (this.form.addNum.length && this.form.operatorMethod === '1') {
                if (FormatReg.test(newVal)) {
                    this.form.addNum = newVal
                } else {
                    this.form.addNum = oldVal
                }
            } else {
                this.form.addNum = ''
            }
        },
        'from.reduceNum': function (newVal, oldVal) {
            let FormatReg = /^\d+$/
            if (this.form.reduceNum.length && this.form.operatorMethod === '2') {
                if (FormatReg.test(newVal)) {
                    this.form.reduceNum = newVal
                } else {
                    this.form.reduceNum = oldVal
                }
            } else {
                this.form.reduceNum = ''
            }
        },
        'form.operatorMethod': function (newVal, oldVal) {
            if (newVal === '1') {
                // 表示增加
                this.form.reduceNum = ''
                this.disabledReduce = true
                this.disabledAdd = false
            } else {
                // 表示减少
                this.form.addNum = ''
                this.disabledAdd = true
                this.disabledReduce = false
            }
        }
    }
}
</script>
<style lang="scss">
.recharge{
    .el-dialog{
        width: 6.6rem;
        .el-dialog__header{
            height: .6rem;
            /* line-height: .6rem; */
            padding-left: .3rem;
            padding-top: .2rem;
            border-bottom: 1px solid #EAEEF0;
            font-size: 20px;
            color: #263238;
        }
    }
    .el-dialog__body{
        padding: 60px 40px 0 50px;
        // .cardText{
        //     font-size: 18px;
        //     color: #263238;
        //     text-align: center;
        // }
        .personInfo{
            display: flex;
            // margin-top: .4rem;
            .personLeft{
                flex: 1;
                text-align: center;
                position: relative;
                &:after{
                    content: "";
                    top: 12px;
                    position: absolute;
                    right: 0px;
                    width: 1px;
                    height: .26rem;
                    background: #E9EDEF;
                }
                img{
                    width: .2rem;
                    vertical-align: middle;
                    margin-right: .1rem;            
                    border-radius: .2rem;
                    height: .2rem;
                }
            }
            .personRight{
                flex: 1;
                text-align: center;
            }
            .fir{
                font-size: .13rem;
                color: #909399;
                margin-bottom: .13rem;
            }
            .sec{
                font-size: .17rem;
                color: #263238;
                span{
                    font-size: .14rem;
                    color: #C0C4CC;
                    margin-left: .1rem;
                    margin-right: .1rem;
                }
            }
        }
        .form{
            border-bottom: 0px !important; 
        }
        .secform{
            margin-top: 0rem !important;
        }
        .bot{
            display: flex;
            justify-content: flex-end;
            flex-flow: column;
            font-family: PingFangSC-Regular;
            font-size: .14rem;
            color: #8597A1;
            text-align: right;
            margin-bottom: .3rem;
            // letter-spacing: 0;
            // line-height: 14px;
            .text{
               line-height: .14rem; 
            }
            .tips{
                font-family: PingFangSC-Medium;
                font-size: .12rem;
                color: #C0C4CC;
                letter-spacing: 0;
                line-height: .12rem;
                margin-top: .08rem;
            }
        }

    }
    .el-input__prefix{
        left: auto;
        right: 0px;
        top: -5px;
    }
    .form{
        margin-bottom: .1rem;
    }
}
.updatecoach .el-dialog__body .el-form .el-form-item__label{
    white-space: nowrap;
    margin-left: 0px;
}
.updatecoach .el-dialog__body .el-form .form{
    .money{
        border: none;
        background: #fff;
        display: flex;
        flex-flow: column;
        .el-form-item__label{
            padding-left: 0;
            margin-left: 0;
        }
        .el-form-item__content{
            display: flex;
            width: 100%;
            margin-left: 0px !important;
            label{
                flex: 1;
            }
        }
    }
}
.moneyUpdate .el-dialog__body .el-form .TextArea{
    height: .8rem !important;
}
.moneyUpdate .el-dialog .el-form .formNew .el-form-item__content{
    .el-input{
        width: 85% !important;
        .el-input__inner{
            width: 100%;
        }
    }
    span{
        white-space: nowrap;
    }
}
.moneyUpdate .el-dialog .el-form .card .el-form-item__content{
    .el-input{
        width: 100% !important;
        .el-input__inner{
            width: 100%;
        }
    }
    span{
        white-space: nowrap;
    }
}
</style>



